import React, { useState, useEffect, useRef } from "react";
import { Container, Row, Col, Image } from "react-bootstrap";
import ReactiveButton from 'reactive-button';
import TextLoop from "react-text-loop";
import { gsap } from "gsap";
import "../styles/MainScreen.css";
import ParticlesLinks from "../components/ParticlesLinks";

const MainScreen = () => {
  const name = 'Eduardo Hernndez';
  const career = 'Mobile Developer';
  const [state, setState] = useState('idle');
  const imgRef = useRef(null);

  useEffect(() => {
    gsap.fromTo(
      imgRef.current,
      { opacity: 0, y: -100 },
      { opacity: 1, y: 0, duration: 1.5, ease: "power3.out" }
    );
  }, []);

  const _downloadPDF = () => {
    const link = document.createElement('a');
    link.href = '/cv/CV_ EduardoHernandez.pdf';
    link.download = 'CV_ EduardoHernandez.pdf';
    link.click();
  };

  const onClickHandler = () => {
    setState('loading');
    setTimeout(() => {
      setState('success');
      _downloadPDF();
    }, 2000);
  };

  return (
    <div id="main" class="main-section">
      <Container fluid="md">
        <Row>
          <Col>
            <div
              style={{
                backgroundColor: 'black',
                height: "500px",
                width: "100%",
                position: "relative",
                overflow: "hidden",
              }}
            >
              <ParticlesLinks id="particles-main" />
              <div
                style={{
                  position: "absolute",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%, -50%)",
                  color: "white",
                  textAlign: "center",
                  zIndex: 1,
                }}
              >
                <div className="box-img-w-title">
                  {/*<Image className="img-cv" src="./assets/cv_photo.png" roundedCircle />*/}
                  <Image
                    ref={imgRef}
                    className="img-cv"
                    src="./assets/cv_photo.png"
                    alt="Foto CV"
                    roundedCircle
                  />
                  <div className="box-name">
                    <p className="name-txt">
                      {name}
                    </p>
                    <p className="name-txt">
                      {career + " "}
                      <TextLoop className="loop-txt" springConfig={{ stiffness: 150, damping: 8 }}>
                        {["React Native", "Android", "iOS"]}
                      </TextLoop>
                    </p>
                  </div>

                  <ReactiveButton
                    buttonState={state}
                    idleText="Descargar CV"
                    loadingText="Cargando"
                    successText="Listo"
                    onClick={onClickHandler}
                    style={{ fontFamily: 'Lucida Console', fontSize: 17, padding: 10 }}
                    color="teal"
                    width={200}
                    shadow
                    rounded
                  />

                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default MainScreen;